<template>
  <v-app>
    <v-navigation-drawer width="300" v-model="drawer" fixed app>
      <navdrawer @pubTemplate="importTemplate" />
    </v-navigation-drawer>
    <v-main>
      <!-- {{ getdate }} -->
      <MainForm
        :handleSort="handleSort"
        :change="change"
        :dragging="dragging"
        :templates="templates"
      />

      <v-hover open-delay="0" v-slot:default="{ hover }">
        <v-snackbar
          v-model="snackbar.snackbar"
          :timeout="hover ? -1 : snackbar.timeout"
          :class="{ 'on-hover': hover }"
          elevation="24"
          top
          left
        >
          Đã thay đổi 1 mục
          <template v-slot:action="{ attrs }">
            <v-btn
              color="yellow"
              text
              v-bind="attrs"
              @click="
                undo();
                snackbar.snackbar = false;
              "
            >
              Hoàn tác
            </v-btn>
          </template>
        </v-snackbar>
      </v-hover>
      
      <!-- <div v-for="(item,i) in oldTemplates" :key="i">
          <div>{{ item }}</div>
        </div> -->
      <!-- <pre>{{ JSON.stringify(this.temp, null, 2) }}</pre> -->
    </v-main>
    <v-footer class="primary" fixed app dark>
      <div style="max-width: 50px">
        <v-app-bar-nav-icon dark @click.stop="drawer = !drawer">
        </v-app-bar-nav-icon>
      </div>
      <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            class="mr-0"
            v-bind="attrs"
            style="max-width: 180px"
            v-on="on"
            background-color="#00000000"
            v-model="nameTemplates"
            loading="false"
            dense
            flat
            solo
            hide-details
            single-line
            placeholder="Tên mẫu"
          />
        </template>
        Tên mẫu
      </v-tooltip>
      <v-spacer></v-spacer>
      <bottombar
        :data="templates"
        :scroll="scrollToBottom"
        :getQID="handleSort"
        @dragging="dragging = $event"
      ></bottombar>
      <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <div v-bind="attrs" v-on="on" rounded>
            <v-btn
              class="ml-4 rounded"
              width="80"
              light
              v-on:click="
                templates.Part.push({
                  Name: 'TÊN PHẦN',
                  Des: 'Mô tả',
                  Question: [],
                });
                handleSort();
                scrollToBottom();
              "
              :ripple="false"
              elevation="0"
            >
              <v-icon color="primary" size="20">mdi-plus</v-icon>
            </v-btn>
          </div>
        </template>
        <span>Thêm phần</span>
      </v-tooltip>
      <v-spacer></v-spacer>
      <div class="mr-2">
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <div v-bind="attrs" v-on="on" rounded>
              <v-btn fab small icon @click="createNew">
                <v-icon>mdi-file</v-icon>
              </v-btn>
            </div>
          </template>
          <span>Tạo mẫu mới</span>
        </v-tooltip>
      </div>
      <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <div v-bind="attrs" v-on="on" rounded>
            <createTemplate
              class="mr-2"
              :nameTemplates="nameTemplates"
              :data="templates"
            ></createTemplate>
          </div>
        </template>
        <span>Xuất bản</span>
      </v-tooltip>
      <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <div v-bind="attrs" v-on="on" rounded>
            <jsonviewer
              class="mr-2"
              :getQID="handleSort"
              :nameTemplates="nameTemplates"
              :data="templates"
              @update-data="importTemplate"
            ></jsonviewer>
          </div>
        </template>
        <span>JSON</span>
      </v-tooltip>
      <!-- <v-tooltip top>
        <template v-slot:activator="{ on, attrs }">
          <div v-bind="attrs" v-on="on" rounded>
            <getdata :change="change" @update-data="importTemplate"></getdata>
          </div>
        </template>
        <span>Nhập Template</span>
      </v-tooltip> -->
      <v-btn small fab icon><v-icon>mdi-account</v-icon></v-btn>
    </v-footer>
  </v-app>
</template>

<script>
import jsonviewer from "./components/dialog/JsonViewer";
import createTemplate from "./components/dialog/CreateTemplate";
import bottombar from "./components/layout/bottom-navigation";
import Sticky from "vue-sticky-directive";
// import getdata from "./components/dialog/GetData";
import MainForm from "./components/layout/MainForm";
import navdrawer from "./components/layout/nav-drawer";

export default {
  directives: {
    Sticky,
  },
  name: "App",

  data: function () {
    return {
      drawer: false,
      temp: [],
      items: ["Xóa phần"],
      snackbar: {
        hover: false,
        snackbar: false,
        text: "",
        timeout: 5000,
      },
      nameTemplates: "untitled",
      templates: {
        templateID: "",
        logo_org:
          "https://lhu.edu.vn/ViewPage/LHUVN/_default/image/truong_dai_hoc_lac_hong_logo.png",
        name_org: "Trường Đại học Lạc Hồng",
        url_finishRedirect: "https://lhu.edu.vn/",
        Title: "TÊN PHIẾU KHẢO SÁT",
        Des: "Mô tả của phiếu khảo sát",
        url_login: "",
        url_template: "",
        Part: [
          {
            Name: "TÊN PHẦN",
            Des: "Mô tả phần",
            Question: [],
          },
        ],
      },
      loadingBtn: false,
      oldTemplates: {},
      dragging: false,
    };
  },
  methods: {
    createNew: function () {
      if (
        confirm(
          "Mẫu khảo sát cũ sẽ bị xóa, hãy chắc chắn bạn đã lưu mẫu trước khi tạo mới !"
        )
      ) {
        this.nameTemplates = "untitled";
        this.templates = {
          templateID: "",
          logo_org:
            "https://lhu.edu.vn/ViewPage/LHUVN/_default/image/truong_dai_hoc_lac_hong_logo.png",
          name_org: "Trường Đại học Lạc Hồng",
          url_finishRedirect: "https://lhu.edu.vn/",
          Title: "TÊN PHIẾU KHẢO SÁT",
          Des: "Mô tả của phiếu khảo sát",
          url_login: "",
          url_template: "",
          Part: [
            {
              Name: "TÊN PHẦN",
              Des: "Mô tả phần",
              Question: [],
            },
          ],
        };
      }
    },
    change() {
      this.oldTemplates = JSON.parse(JSON.stringify(this.templates));
      this.snackbar.snackbar = true;
      // this.templates.Part = JSON.parse(JSON.stringify(this.temp[this.temp.length - 1].model));
    },
    // undo() {
    //   const a = [...this.temp].reverse().findIndex((i) => i.visible);
    //   this.temp[this.temp.length - 1 - a].visible = false;
    //   this.templates.Part = JSON.parse(JSON.stringify(this.temp[this.temp.length - 1 - a].model));
    // },
    undo: function () {
      this.templates = this.oldTemplates;
      this.snackbar.snackbar = false;
    },
    scrollToBottom() {
      let element = document.getElementById("endpoint");
      element.scrollIntoView({ behavior: "smooth", block: "end" });
    },

    handleSort() {
      this.templates.Part.map((value, index) => {
        const partID = ++index * 10000000;
        value.Question.map((e, i) => {
          const key = partID + ++i * 10000;

          if (e.Type === "MarkTable") {
            e.Topic.map((val, j) => {
              val.GroupID = key + ++j * 100;
              val.Question.map((ques, k) => {
                ques.QID = val.GroupID + ++k * 1;
              });
            });
            return;
          }
          if (e.Type === "Radio") {
            e.QID = key;
            e.Option.map((val) => {
              if (val.Question != undefined) {
                val.Question.map((ques, j) => {
                  ques.QID = e.QID + ++j * 1;
                });
              }
            });
            return;
          }
          if (e.Type === "Checkbox") {
            e.GroupID = key;
            e.Option.map((val, j) => {
              val.QID = e.GroupID + ++j * 10;
              if (val.Question != undefined) {
                val.Question.map((ques, k) => {
                  ques.QID = val.QID + ++k * 1;
                });
              }
            });
            return;
          }
          e.QID = key;
        });
      });
    },
    importTemplate(data) {
      this.drawer = false;
      this.editable = true;
      this.templates = data;
    },
  },
  // updated() {
  //   this.scrollToBottom();
  // },
  // mounted() {
  //   this.scrollToBottom();
  // },
  computed: {
    getdate() {
      return this.$store.state.dates
    }
  },
  components: {
    MainForm,
    jsonviewer,
    createTemplate,
    bottombar,
    // getdata,
    navdrawer,
  },
};
</script>

