<template>
  <div id="precommit">
    <v-dialog scrollable v-model="dialog" width="400px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          fab
          small
          v-bind="attrs"
          v-on="on"
          icon
          @click="requestTemplateID"
        >
          <v-icon>mdi-upload</v-icon>
        </v-btn>
      </template>
      <v-card>
        <div>
          <div class="mx-2 mt-2" align="center">
            <v-date-picker
              width="100%"
              locale="vi-VN"
              color="primary"
              header-color="primary"
              v-model="dates"
              :show-current="true"
              @input="fixedDates"
              @change="fixedDates"
              @click="fixedDates"
              range
            ></v-date-picker>
          </div>
          <div class="mx-3 mb-1">
            Mẫu sẽ hiện hành từ
            <b>{{ moment(dates[0]).format("DD/MM/YYYY") }}</b> đến
            <b>{{ dates.length > 1?moment(dates[1]).format("DD/MM/YYYY"):moment(dates[0]).add(10, "days").format("DD/MM/YYYY") }}</b>
          </div>
        </div>
        <div>
          <v-text-field
          class="pa-1"
            label="templateID"
            v-model="data.templateID"
            @change="requestTemplateID"
            @input="requestTemplateID"
            flat
            outlined
            dense
            solo
            hide-details
          ></v-text-field>
        </div>
        <div
          :class="`py-2 text-caption px-3 mx-1 white--text ${block.background} rounded`" v-text="`${block.text}`"
        >
        </div>
        <div>
          <v-toolbar class="text-right py-1" tile dense>
            <v-btn class="text-subtitle-2 mr-1" :disabled="!btns.viewBtn.status" :color="btns.viewBtn.color" v-text="`${btns.viewBtn.text}`" @click="viewBtn" text></v-btn>
            <v-spacer></v-spacer>
            <v-btn class="text-subtitle-2 mr-1" :disabled="!btns.createBtn.status" :color="btns.createBtn.color" v-text="`${btns.createBtn.text}`" @click="createBtn" text></v-btn>
            <v-btn class="text-subtitle-2" :color="btns.backBtn.color" v-text="`${btns.backBtn.text}`" @click="backBtn" text></v-btn>
          </v-toolbar>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";

const token =
  "eyJ1aWQiOiIxMTgwMDAxNDgiLCJzaWQiOiIwQzk0NEREOTA0ODc1NDVEIiwiZXhwIjoyMDgyMDg0NDIwfQ.CS9Q0kUF985zbgu790h3GnYgXmzQf1-ZKUxsqebpRkI";
const instance = axios.create({
  baseURL: "https://tapi.lhu.edu.vn/qa",
});
instance.defaults.headers.common["Authorization"] = token;

export default {
  name: "precommit",
  props: {
    data: Object,
    nameTemplates: String,
  },
  data: function () {
    return {
      // dates: [
        
      // ],
      dialog: false,
      block: {
        color: 'white',
        background: 'white',
        text: ''
      },
      btns: {
        viewBtn: {
          status: false,
          color: 'purple',
          text: 'Xem'
        },
        createBtn: {
          status: false,
          color: 'success',
          text: 'Tạo'
        },
        backBtn: {
          status: true,
          color: 'primary',
          text: 'Trở về'
        }
      },
      moment,
    };
  },
  methods: {
    fixedDates: function () {
      this.requestTemplateID();
      return this.dates.sort(
        (a, b) => new moment(a).format("YYYYMMDD") - new moment(b).format("YYYYMMDD")
      );
    },
    requestTemplateID() {
      if(this.data.templateID.length == 0 || this.data.templateID.indexOf(' ') >= 0){
        this.block.background = 'error';
        this.btns.createBtn.status = false;
        this.btns.viewBtn.status = false;
        this.block.text = 'TemplateID không hợp lệ (không được chứa khoảng trắng hoặc để trống)';
        return
      }else{
        instance
          .post("/Web_KiemTraTemplateID", {
            TemplateID: this.data.templateID,
          }).then((res)=>{
            this.btns.createBtn.status = true;
            this.btns.viewBtn.status = false;
            if(res.data.data[0].error){
              //insert
              this.btns.createBtn.text = 'Cập nhật';
              this.btns.createBtn.color = 'warning';
              this.block.background = 'warning';
              this.btns.viewBtn.status = true;
              this.block.text = res.data.data[0].message + ', cập nhật mẫu ?';
            }else{
              //create
              this.btns.createBtn.text = 'Tạo';
              this.btns.createBtn.color = 'success';
              this.block.background = 'success';
              this.block.text = res.data.data[0].message + ', tạo mẫu mới ?';
            }
          })
      }
    },
    //btn actions
    backBtn: function (){
      this.dialog = false;
    },
    createBtn: function (){
      instance.post("/Web_TemplateUpdate", {
        TemplateID: this.data.templateID,
        TenKhaoSat: this.nameTemplates,
        jsonDataNew: JSON.stringify(this.data),
        NgayBD: moment(this.dates[0]).format("MM-DD-YYYY"),
        NgayKT: this.dates.length > 1 ? moment(this.dates[1]).format("MM-DD-YYYY"):moment(this.dates[0]).add(10, "days").format("MM-DD-YYYY"),
      })
      .then((response) => {
        this.message = response.data.data[0].message;
        this.btns.createBtn.status = false;
        this.btns.viewBtn.status = true;
      })
      .catch((error) => {
        console.log(error);
      });
    },
    viewBtn: function(){
      window.open(
        "https://qa.lhu.edu.vn/" + this.data.templateID + "/1",
        "_blank"
      );
    }
  },
  computed: {
    dates: {
      get () {
        return this.$store.state.dates
      },
      set (value) {
        this.$store.commit('setDates', value)
      }
    }
  }
};
</script>

