<script>
import axios from "axios";

const token =
  "eyJ1aWQiOiIxMTgwMDAxNDgiLCJzaWQiOiIwQzk0NEREOTA0ODc1NDVEIiwiZXhwIjoyMDgyMDg0NDIwfQ.CS9Q0kUF985zbgu790h3GnYgXmzQf1-ZKUxsqebpRkI";
const instance = axios.create({
  baseURL: "https://tapi.lhu.edu.vn/qa",
});
instance.defaults.headers.common["Authorization"] = token;

export default {
  name: "drawer",
  props: {
    draw: Boolean,
  },
  data: function () {
    return {
      datas: [],
      selected: "",
      message: "Có thể tải lên",
      dialog: false,
      templateDelete: "",
    };
  },
  methods: {
    viewForm: function (templateID) {
      window.open("https://qa.lhu.edu.vn/" + templateID + "/1", "_blank");
    },
    requestTemplates() {
      instance.post("/Web_SelectTemplate").then((response) => {
        // console.log(response.data.data.TenKhaoSat)
        this.datas = response.data.data;
      });
    },
    editTemplate(template) {
      this.selected = template.TemplateID;
      this.$emit("pubTemplate", JSON.parse(template.JdataTemplate));
      this.$store.commit(
        "setDates",
        [template.NgayBatDau, template.NgayKetThuc].map(
          (date) => date.split("T")[0]
        )
      );
    },
    delTemplate(TemplateID) {
      // console.log(TemplateID)
      instance
        .post("/Web_TemplateDelete", {
          TemplateID: TemplateID,
        })
        .then((response) => {
          // this.dialog_msg = response.data.data[0].message;
          console.log(TemplateID, response.data.data[0].message);
          this.requestTemplates();
        });
      this.dialog = false;
    },
  },
  created() {
    this.requestTemplates();
  },
  computed: {
    sortedArray: function () {
      return [...this.datas].sort(function (a, b) {
        return a.TemplateID.localeCompare(b.TemplateID);
      });
    },
  },
};
</script>

